@charset "utf-8";

.movie-detail {
  display: flex;
  padding: 40px 0px 0px 200px;
  gap: 40px;
}
.movie-detail img {
  width: 300px;
  height: auto;
  border-radius: 12px;
}
.movie-detail__info {
  max-width: 600px;
}
.movie-detail__info h1 {
  margin-bottom: 10px;
}

.back-button{
  position:relative;
  top:100px;
  padding:5px 10px;
  border:1px solid #ccc;
  background:#333;color:#fff;
}

/* contact 서식 */

.contact{
  width: 600px;
  margin : 0 auto;
  padding-top: 10px;
}
.con_txt{
  padding: 10px 10px;
  margin: 15px 0px;
  background-color: #ccc;
  border: none;
  width: 100%;
}

.con_btn{
  padding: 5px 10px;
  background-color: #333;
  color: #fff;
  border: none;
}

/* api 서식 */
.api{
  text-align: center;
  line-height: 25px;
  margin: 0 auto;

}
.api h2{margin: 20px 0px;}
.api hr{margin:10px 0px}
.api_table {
  border-collapse: collapse;
  transform: translate(40%);
  margin-top: 20px;
}
.api h3{margin: 15px 0px;}

.api_table th,
.api_table td {
  border: 1px solid #ddd;
  padding: 12px 15px;
  text-align: left;
  vertical-align: top;
}
.api_table th{text-align: center;}
.api_table thead {
  background-color: #efeeee;
}


/* 모바일버전 */
@media screen and (max-width: 720px) {
  .con_title{
    transform: translatfeX(-40%);
    margin-bottom: 30px;
  }
  .contact {
    width: 300px;
    margin: 2% -63%;
  }
  .api{
    margin: 0px;
    padding: 0px; 
    width: 100%; 
    overflow-x: hidden;}

  .api h2{padding: 100px 0px 0px 0px;}
  .api_table{
    width: 100%;
    min-width: 400px; 
    border-collapse: collapse;
    transform: translate(0%,10%);
  }
  .api_table th,
  .api_table td {
    border: 1px solid #ddd;
    padding: 12px 15px;
    text-align: left;
}
}








